.card {
  width: 100%;
  max-width: 200px;
  height: 350px;
  max-height: 350px;
  background: #fff;
  border-radius: 0;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.08);
  cursor: pointer;
}

.photoWrap {
  position: relative;
  width: 100%;
  aspect-ratio: 1;
  background: #f5f5f5;
  overflow: hidden;
}

.photo {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.photoNav {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 40px;
  background: rgba(0, 0, 0, 0.2);
  border: none;
  cursor: pointer;
  padding: 0;
  transition: background 0.2s ease;
}

.photoNav:hover {
  background: rgba(0, 0, 0, 0.35);
}

.photoNav::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  width: 10px;
  height: 10px;
  border-left: 2px solid #fff;
  border-bottom: 2px solid #fff;
  transform: translate(-30%, -50%) rotate(45deg);
}

.photoNavPrev {
  left: 0;
}

.photoNavPrev::after {
  transform: translate(-70%, -50%) rotate(-135deg);
}

.photoNavNext {
  right: 0;
}

.photoNavNext::after {
  transform: translate(-30%, -50%) rotate(45deg);
}

.dots {
  position: absolute;
  bottom: 10px;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  gap: 6px;
}

.dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  border: none;
  padding: 0;
  background: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  transition: background 0.2s ease;
}

.dot:hover {
  background: rgba(255, 255, 255, 0.9);
}

.dotActive {
  background: #fff;
}

.discountBadge {
  position: absolute;
  top: 10px;
  right: 10px;
  background: #000;
  color: #fff;
  font-size: 0.75rem;
  font-weight: 700;
  padding: 4px 8px;
  border-radius: 0;
}

.body {
  padding: 1rem 1.25rem;
  flex: 1;
  min-height: 0;
  display: flex;
  flex-direction: column;
}

.name {
  font-size: 1.1rem;
  font-weight: 700;
  margin: 0 0 0.5rem;
  color: #000;
  text-transform: lowercase;
  flex-shrink: 0;
}

.description {
  font-size: 0.9rem;
  color: #333;
  margin: 0 0 0.75rem;
  line-height: 1.4;
  /* Ровно 2 строки, остальное обрезается с ... */
  height: 2.8em;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  flex-shrink: 0;
}

.prices {
  display: flex;
  align-items: baseline;
  gap: 0.5rem;
  margin-bottom: 0;
  flex-shrink: 0;
}

.price {
  font-size: 1.25rem;
  font-weight: 700;
  color: #000;
}

.crossedPrice {
  font-size: 0.95rem;
  color: #888;
  text-decoration: line-through;
}

